import { GSP, Package } from '@farris/ide-devkit';
import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import { InternalServicePackageView } from './views';
import { InternalServiceComponent } from './views/internalservicedesigner/internalservice.component';

export class InternalServicePackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  
  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openSg(uri));
  }

  openSg(uri) {
    if (uri.endsWith('.iapi')) {
      const pkgView = new InternalServicePackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as InternalServiceComponent;
      currentElement.metadataUri = uri;
      // 这里需要处理uri，title应返回元数据名称，
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }
}


