import { ComponentFactoryResolver, Injector } from '@angular/core';
import { PackageView } from '@farris/ide-devkit';
import { InternalServiceComponent } from './internalservicedesigner/internalservice.component';
import { InternalServiceModule } from './internalservicedesigner/internalservice.module';

export class InternalServicePackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(InternalServiceComponent);
    const moduleRef = this.injector.get(InternalServiceModule) as InternalServiceModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}



