import { Component, OnInit, Input } from '@angular/core';
import { Headers, RequestOptions, Http } from '@angular/http';
import { isNullOrUndefined } from 'util';
import { InternalApiMetadata } from './internalapimetadata/internalapimetadata';
import { host } from './internalcommonobject/internalglobal';
import { MetadataDto, GSPMetadataService } from '../../../../../reference/metadata/metadata.ref';
import { CacheService } from '../../../../../reference/farris/farris.idedevkit.ref';

@Component({
  selector: 'app-internalapi',
  templateUrl: './internalservice.component.html',
  styleUrls: ['./internalservice.component.css']
})
export class InternalServiceComponent implements OnInit {
  title = 'app';
  public apimetadata: InternalApiMetadata;
  public metadataDto: MetadataDto;
  private _metadataContnet: MetadataDto;
  private _metadataUri:string;
  public resourceTypeList: string[] = []; //资源类型列表

  set metadataUri(metadataUri: string) {
    this._metadataUri = metadataUri;
    // todo: 通过元数据服务获取元数据内容
    const fileName = metadataUri.substring(metadataUri.lastIndexOf('/') + 1);
    const filePath = metadataUri.substring(1, metadataUri.lastIndexOf('/'));
    this.metadataService.LoadMetadata(fileName, filePath).subscribe(value => {
      this.metadataContent = value;
    });
  }

  set metadataContent(metadataContent: any) {
    this._metadataContnet = metadataContent;
    this.loadSgMetadata(metadataContent);
  }

  constructor(private http: Http, private metadataService: GSPMetadataService,private cache:CacheService) { }
  
  ngOnInit() {
    this.apimetadata=new InternalApiMetadata();
    this.getResourceTypeList();
  }

  //获取资源类型列表
  private getResourceTypeList() {
    if (this.resourceTypeList.length > 0) {
      return;
    }
    if (this.resourceTypeList.length == 0) {
      let url = host + "/api/dev/main/v1.0/internalapi/resource-types";
      this.http.get(url).toPromise().then(response => {
        if (response.json()) {
          this.resourceTypeList = response.json();
        }
      })
    }
  }

  //加载SG元数据
  private loadSgMetadata(dto: MetadataDto) {
    this.metadataDto = dto;
    if (dto.content && dto.content != "") {
      let jsonObj = JSON.parse(dto.content);
      this.GetCamelParse(jsonObj);
      this.apimetadata = jsonObj;
      if (isNullOrUndefined(this.apimetadata.code)) {
        this.apimetadata.code = dto.code;
      }
      if (isNullOrUndefined(this.apimetadata.name)) {
        this.apimetadata.name = dto.name;
      }
      if (isNullOrUndefined(this.apimetadata.version)) {
        this.apimetadata.version = '1.0';
      }
      if (isNullOrUndefined(this.apimetadata.bizObject)) {
        this.apimetadata.bizObject = dto.bizobjectID;
      }
    }
  }

  //json串解析
  private GetCamelParse(jsonObj: any) {
    for (let key in jsonObj) {
      if (typeof (jsonObj[key]) === "object") {
        this.GetCamelParse(jsonObj[key]);
      }
      let temp = jsonObj[key];
      delete (jsonObj[key]);
      jsonObj[this.ConvertToCamel(key)] = temp;
    }
  }

  //字符串转换为Camel
  private ConvertToCamel(input: string): string {
    if (input.length > 0) {
      let len = input.length;
      var strTemp = ""; //新字符串
      for (let i = 0; i < len; i++) {
        if (i == 0) {
          strTemp += input[i].toLowerCase(); //第一个
        }
        else {
          strTemp += input[i];
        }
      }
      return strTemp;
    }
  }

  //保存Api元数据
  private SaveApiMetadata() {
    let stringcontent = JSON.stringify(this.apimetadata);
    this.metadataDto.content = stringcontent;
    let sessionId=this.cache.get('sessionId');
    this.metadataService.SaveMetadata(this.metadataDto,sessionId).subscribe(response=>{
        if(response.status==200){
           alert("保存成功！");
        }
        else{
          alert("保存失败!");
        }
    })
  }
 
}



