import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { isNullOrUndefined } from 'util';
import { InternalServiceModel, InternalApiMetadata } from '../../internalapimetadata/internalapimetadata';
import { ObjectDict } from '../../internalcommonobject/internalapisummary';

interface Item {
    text: string,
    value: number
  }

@Component({
    selector: 'kendo-grid-edit-form',
    styles: [
        'input[type=text] { width: 100%; }'
    ],
    templateUrl: "./internalapibaseinfodialog.component.html"
})
export class SgModelModalComponent implements OnChanges{
    private active = false;
    private modelType: Array<Item> = [ 
        { text: "VM", value:0 },
        { text: "Dto", value:1 }
    ];

    @Input() public newSgModel:InternalServiceModel;
    @Input() apimetadata: InternalApiMetadata;

    @Output() save: EventEmitter<InternalServiceModel> = new EventEmitter();

    ngOnChanges(changes: SimpleChanges): void {
        if(this.newSgModel!=undefined){
            this.active = true;
        }
    }

    public onSave(e): void {
        if(isNullOrEmptyString(this.newSgModel.modelId)||isNullOrUndefined(this.newSgModel.modelId)){
            alert("请输入模型ID");
            return;
        }
        if(isNullOrEmptyString(this.newSgModel.code)||isNullOrUndefined(this.newSgModel.code)){
            alert("请输入模型编号");
            return;
        }
        if(isNullOrEmptyString(this.newSgModel.name)||isNullOrUndefined(this.newSgModel.name)){
            alert("请输入模型名称");
            return;
        }
        if(isNullOrUndefined(this.newSgModel.modelType)){
            alert("请输入模型类型");
            return;
        }
        let validResultDict = this.serviceModelCheck();
        if (validResultDict.key == "false") {
            alert(validResultDict.value);
            return;
        }
        this.save.emit(this.newSgModel);
        this.newSgModel=undefined;
        this.active = false;
    }

    public onCancel(e): void {
        e.preventDefault();
        this.closeForm();
    }

    private closeForm(): void {
        this.active = false;
    }

    serviceModelCheck() {
        let validResultDict = {} as ObjectDict<string>;
        let refTemp = false;
        if (this.apimetadata && this.apimetadata.models && this.apimetadata.models.length > 0) {
            let tempIndex = this.apimetadata.models.findIndex(value => value.modelId == this.newSgModel.modelId);
            if (tempIndex != (-1)) {
                validResultDict.key = "false";
                validResultDict.value = "服务模型" + this.newSgModel.code + "已存在。";
                return validResultDict;
            }
            let tempIndex1 = this.apimetadata.models.findIndex(value => value.code == this.newSgModel.code);
            if (tempIndex1 != (-1)) {
                validResultDict.key = "false";
                validResultDict.value = "服务模型" + this.newSgModel.code + "编号重复。";
                return validResultDict;
            }
        }
        if (refTemp == false) {
            validResultDict.key = "true";
            validResultDict.value = "";
        }
        return validResultDict;
    }
}



