export class InternalApiMetadata {
    id:string;
    code:string;
    name:string;
    version:string;
    bizObject:string;
    description:string;
    criticalApplication:string;
    serviceUnit:string;
    routeUrl:string;
    service:InternalServiceDefine;
    models:InternalServiceModel[];
}

export class InternalServiceDefine{
    id:string;
    resourceId:string;
    resourceType:string;
    resourceCode:string;
    resourceName:string;
    resourcePath:string;
    operations:InternalServiceOperation[];
}

export class InternalServiceOperation{
    id:string;
    code:string;
    name:string;
    description:string;
    isDeprecated:boolean;
    resourceOpId:string;
    parameters:InternalServiceParameter[];
    transactionOption:TransactionOption;
    extend1:string;
    extend2:string;
    AssemblyInfo:AssemblyInfo;
}

export class AssemblyInfo{
    AssemblyName:string;
    FullName:string;
}

export class InternalServiceParameter{
    id:string;
    code:string;
    name:string;
    description:string;
    isRetVal:boolean;
    isCollection:boolean;
    isBasicType:boolean;
    modelId:string;
    modelName:string;
    listDeepth:number;
    isRequired:string;
}

export enum TransactionOption{
    requiresNew,
    required,
    notSupport
}

export class InternalServiceModel {
    id:string;
    code:string;
    name:string;
    modelId:string;
    modelType:InternalServiceModelType;
    modelTypeName:string;
}

export enum InternalServiceModelType{
    VM,
    Dto
}

export class ResourceSummary{
    id:string;
    code:string;
    name:string;
}


