import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { CommonModule } from '@angular/common';
import { ToolBarModule } from '@progress/kendo-angular-toolbar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ButtonModule, ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';
import { LayoutModule, SplitterModule } from '@progress/kendo-angular-layout';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { GridModule } from '@progress/kendo-angular-grid';
import { DialogModule } from '@progress/kendo-angular-dialog';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { DropDownsModule, DropDownListModule } from '@progress/kendo-angular-dropdowns';
import { PopupModule } from '@progress/kendo-angular-popup';
import { ReactiveFormsModule } from '@angular/forms';
import { InternalServiceComponent } from './internalservice.component';
import { ServiceDefineComponent } from './internalservicedefine/internalservicedefine.component';
import { InternalResTypeSelectionComponent } from './internalservicedefine/modal/internalresourceselection.component';
import { IsApiBaseInfoComponent } from './internalapiBaseInfo/internalapibaseinfo.component';
import { SgModelModalComponent } from './internalapiBaseInfo/internalapibaseinfomodal/internalapibaseinfodialog.component';
import { InternalParamEditDialogComponent } from './internalservicedefine/modal/internalparamedit.component';
import { InternalResOpSelectionComponent } from './internalservicedefine/modal/internalresourceopselection.component';
import { MetadataSelectModule, GSPMetadataServiceModule } from '@gsp-lcm/metadata-selector';
import { environment } from '../../../../environments/environment';

@NgModule({
  declarations: [
    InternalServiceComponent,
    IsApiBaseInfoComponent,
    ServiceDefineComponent,
    SgModelModalComponent,
    InternalParamEditDialogComponent,
    InternalResTypeSelectionComponent,
    InternalResOpSelectionComponent
  ],
  imports: [
    FormsModule,
    CommonModule,
    ToolBarModule,
    ButtonsModule,
    ButtonGroupModule,
    DialogModule,
    ReactiveFormsModule,
    InputsModule,
    DropDownListModule,
    PopupModule,
    TreeViewModule,
    LayoutModule,
    GridModule,
    LayoutModule,
    MetadataSelectModule,
    GSPMetadataServiceModule.forRoot(environment.SERVER_IP)
  ],
  providers: [],
  entryComponents:[InternalResTypeSelectionComponent,InternalResOpSelectionComponent,InternalParamEditDialogComponent],
  bootstrap: [InternalServiceComponent]
})
export class InternalServiceModule { }



